@import url(https://cdn.jsdelivr.net/gh/aymanbagabas/iosevka-fonts@v6.1.2/dist/iosevka/iosevka.min.css);
body {
  margin: 0;
  font-family:  'Iosevka Web', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.about{width:60vw;margin:5rem auto 0}h2,h3{color:#051622;background-color:#deb992;display:inline-block;padding:.1rem .3rem;border-radius:.2rem}
.project-preview{border:1px solid;max-width:20%;max-height:30%;padding:.5rem;-webkit-flex-grow:1;flex-grow:1;border-radius:5px;color:inherit;text-decoration:none}.project-preview .project-name{border-bottom:1px solid;padding-bottom:.5rem}
.projects-container{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;padding:1rem 3rem;-webkit-justify-content:space-between;justify-content:space-between;height:65vh}.projects-container .filter-container{border:1px solid;position:relative;padding:2rem 1rem}.projects-container .filter-container .title{position:absolute;top:-2rem}.projects-container .filter-container .radio-container{display:-webkit-flex;display:flex;-webkit-justify-content:space-around;justify-content:space-around}.projects-container .project-preview-container{display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap;grid-gap:1rem;gap:1rem;-webkit-justify-content:left;justify-content:left;border:1px solid;min-height:40vh;position:relative;padding:2rem 1rem}.projects-container .project-preview-container .title{position:absolute;top:-2rem}

.footer{height:10vh;position:absolute;bottom:0;display:-webkit-flex;display:flex;width:100vw;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}
.header{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;height:10vw;-webkit-align-items:center;align-items:center;vertical-align:middle;padding:0 5rem}.header .title-container{font-size:2rem;border:1px solid;padding:.5rem;box-shadow:0 0 6px}.header span{cursor:pointer}.header .options{-webkit-flex-grow:1;flex-grow:1;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:flex-end;justify-content:flex-end;grid-gap:2rem;gap:2rem}.header .options .option{color:#deb992;text-decoration:none}.header .options .option:hover{color:#051622;background-color:#deb992}
body {
  background: #051622; /*000C66;*/
  color: #DEB992;
}

a {
  color: #1BA098;
}
